@media all and (min-width: 320px) {
    .video-container {
        width: 100%;
        max-height: 639px;
        height: 100%;
        border-radius: 24px;
        overflow: hidden;
        position: relative;
        display: flex;
        &:hover {
            .controls {
                transform: translateY(0);
            }
        }
        .btn-play-main {
            width: 60px;
            height: 60px;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            background-color: #fff;
            border-radius: 50%;
            box-shadow: $secondary-shadow;
            &:focus {
                outline: none;
                box-shadow: $secondary-shadow;
                box-shadow: rgba(0, 0, 0, 0.6)
            }
            img {
                width: 20px;
            }
        }
        .button-main-hidden {
            display: none;
        }
    }

    .video {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }

    .controls {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 45px;
        background-color: rgba(0, 0, 0, 0.3);
        transform: translateY(100%) translateY(-3px);
        transition: all 0.5s;
        progress {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 5px;
            color: #496ADC;
            background-color: #000;
            &::-moz-progress-bar {
                color: #496ADC;
                background-color: #000;
            }
            
            &::-webkit-progress-bar {
                color: #496ADC;
                background-color: #000;
            }
        }
        .buttons {
            padding: 10px;
        }
        .play-pause {
            background: none;
            border: 0;
            outline: 0;
            &::before {
                content: "";
                display: inline-block;
                width: 18px;
                height: 23px;
                background-image: url("../img/video/icon-play.png");
                background-repeat: no-repeat;
                background-position: center;
                background-size: cover;
            }
        }
        .play::before {
            content: "";
            background-image: url("../img/video/icon-play.png");
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
        }
        .pause::before {
            content: "";
            background-image: url("../img/video/icon-pause.png");
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
        }
        .stop {
            background: none;
            border: 0;
            outline: 0;
            &::before {
                content: "";
                display: inline-block;
                width: 18px;
                height: 23px;
                background-image: url("../img/video/icon-stop.png");
                background-repeat: no-repeat;
                background-position: center;
                background-size: cover;
            }
        }
        .mute {
            background: none;
            border: 0;
            outline: 0;
            &::before {
                content: "";
                display: inline-block;
                width: 23px;
                height: 23px;
                background-image: url("../img/video/icon-mute-on.png");
                background-repeat: no-repeat;
                background-position: center;
                background-size: cover;
            }
        }
        .mute-on::before {
            content: "";
            background-image: url("../img/video/icon-mute-on.png");
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
        }
        .mute-off::before {
            content: "";
            background-image: url("../img/video/icon-mute-off.png");
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
        }
    }
}

@media all and (min-width: 576px) {
    .video-container {
        .btn-play-main {
            width: 96px;
            height: 96px;
            img {
                width: 29px;
            }
        }
    }
}
